

.menubar{
    height: 80px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;

    
    & .logo{
        margin-left: 50px;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: flex-start;
    }
   
    & .nav{
        display: flex;
        gap: 300px;
        height: 80px;
        padding-right: 200px;
        font-family: 'Cascadia Code', sans-serif;
        align-items: center;
        font-size: 20px;
    }
}